<template>
	<div class="musicList">
		<div 
			class="music"
			v-for="(item,index) in list"
			:key="index"
		>
			{{item.name}}
		</div>
		<div class="btn">增加</div>
	</div>
</template>

<script>
export default{
	data(){
		return{
			list : []
		}
	},
	methods:{
		
	},
	mounted() {
		this.list = this.$store.state.music
	}
}
</script>

<style scoped>
.musicList{
	position: absolute;
	margin-top: 10px;
	max-width: 100vw;
	box-shadow: var(--box-shadow1);
	border-radius: 10px;
	background-color: #FFFFFF;
	overflow: hidden;
	z-index: 99;
}
.musicList .music{
	white-space: nowrap;
	text-align: center;
	padding: 5px;
	cursor: pointer;
	user-select: none;
}
.musicList .music:hover{
	background-color: var(--tint-gray);
}
.musicList .btn{
	width: 70%;
	margin: 5px 0;
	margin-left: 15%;
}
</style>



